<template>
      <div id="otpModal" class="modal fade">
            <div class="modal-dialog">
                  <div class="modal-content">
                        <div class="modal-body text-center">
                              <h2 class="text-bold" style="margin-top: 50px">Input Pin to confirm Transaction</h2>
                              <h4 class="text-grey">Enter the 4 - digits OTP sent to<br>
                                    your phone and email.</h4>
                              <br>
                              <br>
                              <div class="form-group row">
                                    <div class="col-xs-3 col-sm-2 col-sm-offset-2 pin-div">
                                          <div class="input-group">
                                                <input ref='first' v-on:keyup="keymonitor(1)" type="text"
                                                       class="form-control pin" maxlength="1" autofocus/>
                                          </div>
                                    </div>
                                    <div class="col-xs-3 col-sm-2 pin-div no-margin">
                                          <div class="input-group">
                                                <input ref='second' v-on:keyup="keymonitor(2)" type="text"
                                                       class="form-control pin" maxlength="1"/>
                                          </div>
                                    </div>
                                    <div class="col-xs-3 col-sm-2 pin-div no-margin">
                                          <div class="input-group">
                                                <input ref='third' v-on:keyup="keymonitor(3)" type="text"
                                                       class="form-control pin" maxlength="1"/>
                                          </div>
                                    </div>
                                    <div class="col-xs-3 col-sm-2 pin-div no-margin">
                                          <div class="input-group">
                                                <input ref='fourth' v-on:keyup="keymonitor(4)" type="text"
                                                       class="form-control pin" maxlength="1"/>
                                          </div>
                                    </div>
                              </div>
                              <a class="text-danger" @click="clearOTP()" id="clearPin">Clear</a>
                              <br><br>

                              <a class="text-primary" href="#">Resend pin in 60 seconds</a>
                              <br>
                              <br>
                              <br>
                              <br>
                              <div class="row">
                                    <div class="col-xs-10 col-xs-offset-1">
                                          <button type="button" class="btn btn-block btn-primary btn-patricia text-bold"
                                                  data-dismiss="modal" @click="confirm">
                                                Confirm
                                          </button>
                                    </div>
                              </div>
                              <div class="modal-footer">
                                    <br>
                                    <button type="button" class="btn btn-link" data-dismiss="modal"><i
                                            class="fa fa-times"></i>
                                    </button>
                              </div>
                        </div>
                  </div>
            </div>
      </div>
</template>

<script>
    import PButton from '@/components/ui/PButton';

    export default {
        props: ['openModal', 'confirmClick'],

        components: {
            'p-button': PButton,
        },

        methods: {
            keymonitor(event) {
                let reference = ['first', 'second', 'third', 'fourth'];
                //addvalue to state here to process using the ref!
                let next = reference[event];
                if (event < 4) {
                    this.$refs[next].focus()
                }
            },
            clearOTP() {
                let pin = $('.pin');
                pin.val("");
                pin[0].focus();
            },

            confirm() {
                if (this.openModal != null) {
                    $(this.openModal).modal('show');
                } else {
                    this.confirmClick();
                }
            }
        },

        mounted() {

        }
    }
</script>
