<template>
    <div id="upload_card" class="modal fade">
            <div class="modal-dialog modal-lg	">
                <div class="modal-content">
                            <div class="modal-body p-20 mr-10 ml-10">
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                            <br>
                            <h2 class="text-bold text-default mt-20">Upload Cards</h2>
                            <h5 class="text-grey">Upload card(s) screenshots</h5>
                            <br>
                            <div class="row">
                                <div class="col-sm-10 col-sm-offset-1">
                                    <vue-dropzone :use-custom-dropzone-options="true" id="dropzone" class="long-drop" :options="dropzoneOptions"> </vue-dropzone>
                                </div>
                                <div class="col-xs-10 mt-20 col-xs-offset-1">
                                          <button type="button" class="btn btn-block btn-primary btn-patricia text-bold"
                                                  data-dismiss="modal" @click="confirm">
                                                Upload
                                          </button>
                                    </div>
                            </div>

                        </div>
                </div>
            </div>
        </div>
</template>

<script>
    import PButton from '@/components/ui/PButton';
    import 'vue2-dropzone/dist/vue2Dropzone.min.css';
    import vue2Dropzone from 'vue2-dropzone'
    export default {

        props: ['openModal', 'confirmClick'],
        components: {
            'p-button': PButton,
            'vue-dropzone': vue2Dropzone
        },
        data () {
            return {
                dropzoneOptions: {
                    url: 'https://httpbin.org/post',//post URL
                    thumbnailWidth: 200,
                    addRemoveLinks: true,
                    dictDefaultMessage: "Click here or <br> Drag and drop files here."
                }
            }
        },
        methods:{
            confirm() {
                if (this.openModal != null) {
                    $(this.openModal).modal('show');
                } else {
                    this.confirmClick();
                }
            }
        }
    }
</script>

<style scoped>
    .long-drop{
        height: 300px !important ;
        overflow-y: scroll;
    }

</style>

