<template>
      <div>
            <div class="sidebar-category sidebar-category-visible">
                  <div class="category-content no-padding">
                        <br>
                        <ul class="navigation navigation-main navigation-accordion">

                              <li id="dash-nav" @click="closePanel" :class="{ active: isActive('dashboard') }">
                                    <router-link to="/">
                                          <img class="nav-menu-icon" src="../../../assets/svg/icons/overview_icon.svg"/>
                                          <span>Dashboard</span></router-link>
                              </li>
                              <li @click="closePanel" :class="{ active: isActive('transactions') }">
                                    <router-link to="/transactions">
                                          <img class="nav-menu-icon"
                                               src="../../../assets/svg/icons/transactions_icon.svg"/>
                                          <span>Transactions</span></router-link>
                              </li>
                              <li @click="closePanel" :class="{ active: isActive('wallet') }">
                                    <router-link to="/wallet">
                                          <img class="nav-menu-icon" src="../../../assets/svg/icons/wallet_icon.svg"/>
                                          <span>Wallet</span></router-link>
                              </li>
                              <li @click="closePanel" :class="{ active: isActive('products') }">
                                    <router-link to="/dashboard">
                                          <img class="nav-menu-icon" src="../../../assets/svg/icons/products_icon.svg"/>
                                          <span>Products</span></router-link>
                              </li>
                              <li @click="closePanel" :class="{ active: isActive('rates') }">
                                    <router-link to="/dashboard">
                                          <img class="nav-menu-icon" src="../../../assets/svg/icons/rates_icon.svg"/>
                                          <span>Rates</span></router-link>
                              </li>
                              <li @click="closePanel" :class="{ active: isActive('settings') }">>
                                    <router-link to="/dashboard">
                                          <img class="nav-menu-icon" src="../../../assets/svg/icons/settings_icon.svg"/>
                                          <span>Settings</span></router-link>
                              </li>

                        </ul>
                  </div>
            </div>
      </div>
</template>

<script>
    export default {
        name: 'slide-out-panel',
        data() {
            return {};
        },
        methods: {
            closePanel() {
                this.$emit("closePanel", {});
            },

            isActive(page) {
                if (this.$route.path === '/' || this.$route.path.indexOf('trade') !== -1){
                    //Add active to dashboard manually
                    $('#dash-nav').addClass('active');
                } else{
                    $('#dash-nav').removeClass('active');
                    return this.$route.path.indexOf(page) !== -1;
                }
            }

        },
    }
</script>
