            changeBank(bank) {
                this.selectedWithdrawBank = bank;
            },

            //Changes the active tabs onclick of btns
            changeActiveTab(tab){
                let deposit = document.getElementById('tab-deposit');
                let transfer = document.getElementById('tab-transfer');
                let withdraw = document.getElementById('tab-withdraw');

                if (tab === 'deposit'){
                    deposit.classList.add('active');
                    transfer.classList.remove('active');
                    withdraw.classList.remove('active');
                }

                if (tab === 'transfer'){
                    transfer.classList.add('active');
                    deposit.classList.remove('active');
                    withdraw.classList.remove('active');
                }

                if (tab === 'withdraw'){
                    withdraw.classList.add('active');
                    deposit.classList.remove('active');
                    transfer.classList.remove('active');
                }
            }
        }
    }
</script>

