<script>
    export default {
        name: "NavBar",
        data() {
            return {
                activeIcon: 'vert',
                menu_icon: require("../../../assets/svg/icons/menu_icon_vert.svg"),
            }
        },
        methods: {
            toggleMenuIcon: function () {
                if (this.activeIcon === 'vert') {
                    this.menu_icon = require("../../../assets/svg/icons/menu_icon_hori.svg");
                    this.activeIcon = 'hori';

                    //Remove Margin on small sidebar
                    let elements = document.getElementsByClassName('nav-menu-icon');
                    for (let el of elements) {
                        if (el.parentNode.parentNode.className === 'active') {
                            // Don't add for an active nav element
                            el.style.marginLeft = '-10px';
                        } else {
                            el.style.marginLeft = 0;
                        }
                    }

                } else if (this.activeIcon === 'hori') {
                    this.menu_icon = require("../../../assets/svg/icons/menu_icon_vert.svg");
                    this.activeIcon = 'vert';

                    //Add Margin back on large sidebar
                    let elements = document.getElementsByClassName('nav-menu-icon');
                    for (let el of elements) {
                        if (el.parentNode.parentNode.className === 'active') {
                            // Don't add for an active nav element
                            el.style.marginLeft = 0;
                        } else {
                            el.style.marginLeft = '35px';
                        }
                    }

                }
            },


            btnShowPanel() {
                this.$showPanel({
                    component: 'slide-out-panel',
                    width: '60%', //custom width
                    openOn: 'left',
                })
            }

        }

    }
</script>

