<script>
    export default {
        name: 'SideBar',

        methods: {
            isActive(page) {
                if (this.$route.path === '/' || this.$route.path.indexOf('trade') !== -1){
                    //Add active to dashboard manually
                    $('#dash-nav').addClass('active');
                } else{
                    $('#dash-nav').removeClass('active');
                    return this.$route.path.indexOf(page) !== -1;
                }
            }
        }
    }
</script>

