import Vue from 'vue'
import Router from 'vue-router'

// Auth
import Login from '../views/auth/Login'
import RegisterName from '../views/auth/RegisterName'
import RegisterEmail from '../views/auth/RegisterEmail'
import RegisterPhone from '../views/auth/RegisterPhone'
import RegisterPassword from "../views/auth/RegisterPassword";
import RegisterBank from "../views/auth/RegisterBank";
import ConfirmReg from "../views/auth/ConfirmReg";

//Dashboard
import Dashboard from "../views/Dashboard"

//Transactions
import Transactions from "../views/Transactions"

//Wallet
import Wallet from "../views/Wallet"

//GiftCards
import TradeGiftcards from "../views/Giftcards/Trade"
import Giftcards from "../views/Giftcards/Home"

//Bitcoin
import Bitcoin from "../views/Bitcoin/Index";

//PerfectMoney
import PerfectMoney from "../views/PM/Index";

//PayPal
import Paypal from "../views/Paypal/Index";



Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/login',
            name: 'Login',
            meta: {layout: 'no-navbar'}, //For Pages without Navbars, Don't use any meta for pages with Navbars like dashboard route below..
            component: Login
        },
        {
            path: '/register/name',
            name: 'Register-Name',
            meta: {layout: 'no-navbar'},
            component: RegisterName
        },
        {
            path: '/register/email',
            name: 'Register-Email',
            meta: {layout: 'no-navbar'},
            component: RegisterEmail
        },
        {
            path: '/register/phone',
            name: 'Register-Phone',
            meta: {layout: 'no-navbar'},
            component: RegisterPhone
        },
        {
            path: '/register/password',
            name: 'Register-Password',
            meta: {layout: 'no-navbar'},
            component: RegisterPassword
        },
        {
            path: '/register/bank',
            name: 'Register-Bank',
            meta: {layout: 'no-navbar'},
            component: RegisterBank
        },
        {
            path: '/register/confirm',
            name: 'ConfirmReg',
            meta: {layout: 'no-navbar'},
            component: ConfirmReg
        },

        {
            path: '/',
            name: 'Dashboard',
            component: Dashboard
        },
        {
            path: '/transactions',
            name: 'transactions',
            component: Transactions
        },
        {
            path: '/wallet',
            name: 'wallet',
            component: Wallet
        },
        {
            path: '/giftcards/trade',
            name: 'TradeGiftcards',
            component: TradeGiftcards
        },
        {
            path: '/giftcards',
            name: 'Giftcards',
            meta: {layout: 'no-navbar'},
            component: Giftcards
        },
        {
            path: '/bitcoin/trade',
            name: 'Bitcoin',
            component: Bitcoin
        },
        {
            path: '/pm/trade',
            name: 'PerfectMoney',
            component: PerfectMoney
        },
        {
            path: '/paypal/trade',
            name: 'Paypal',
            component: Paypal
        },
    ]
})
