<script>
    import PButton from '@/components/ui/PButton';
    import OTP from '@/components/ui/Modals/Otp';
    import Success from '@/components/ui/animations/success';
    import Upload from '@/components/ui/Modals/Upload';

    export default {
        components: {
            'p-button': PButton,
            'OTP': OTP,
            'success': Success,
            'upload' : Upload
        },
        data() {
            return {
                activeTab: 'Buy',
                successMsg: '',
                showConfirmTransactionBuy: false,
                showConfirmTransactionSell: false,
                showTransactionDone: false,
                selectedDepositMethod: {
                    name: 'Naira Wallet',
                    icon: require('../../assets/svg/btc/naira.svg'),
                },


                depositMethods: [
                    {
                        name: 'Naira Wallet',
                        icon: require('../../assets/svg/btc/naira.svg'),
                    },
                    {
                        name: 'Bank Account',
                        icon: require('../../assets/svg/icons/bank_account.svg'),
                    },
                ],

            }
        },

        methods: {

            changeDepositMethod(method) {
                this.selectedDepositMethod = method;
            },

            toggleStat(tab) {
                if (this.activeTab !== tab) {
                    if (this.activeTab === "Sell") {
                        this.activeTab = "Buy";
                    } else {
                        this.activeTab = "Sell";
                    }
                }
            },

            buyPM() {
                // For animations, Abeg no touch am oo, write ur codes underneath...
                const transactionInputs = document.querySelector('#transaction-inputs');
                transactionInputs.classList.add('animated', 'slideOutLeft');
                self = this;
                setTimeout(() => {
                    self.showConfirmTransactionBuy = true;
                    const buyTransactionStat = document.querySelector('#buy-transaction-stat');
                    buyTransactionStat.classList.remove('fadeInRight');
                    buyTransactionStat.classList.add('slideInRight');
                }, 200);


            },

            sellPM() {
                // For animations, Abeg no touch am oo, write ur codes underneath...
                const transactionInputs = document.querySelector('#transaction-inputs');
                transactionInputs.classList.add('animated', 'slideOutLeft');
                self = this;
                setTimeout(() => {
                    self.showConfirmTransactionSell = true;
                    const buyTransactionStat = document.querySelector('#sell-transaction-stat');
                    buyTransactionStat.classList.remove('fadeInRight');
                    buyTransactionStat.classList.add('slideInRight');
                }, 200);


            },

            showSuccess() {
                this.showConfirmTransactionBuy = false;
                this.showConfirmTransactionSell = false;
                this.showTransactionDone = true;

                if (this.activeTab === 'Buy') {
                    this.successMsg = "0.0334 PM  will be deposited to your Account in 6 hours. Check Dashboard for more details.";
                } else if (this.activeTab === 'Sell') {
                    if (this.selectedDepositMethod.name === 'Naira Wallet') {
                        this.successMsg = "$800.2  will be deposited to USD Wallet instantly. Check Dashboard for more details."
                    } else if(this.selectedDepositMethod.name === 'Bank Account'){
                        this.successMsg = "$800.2  will be sent to your Bank Account instantly. Check Bank for more details."
                    }
                }
            }

        }
    }

</script>

